/* Variables globales pour les styles */
:root {
    --couleur-texte: rgb(0, 225, 255);
    --couleur-titre: gold;
    --taille-texte: 19px;
    --taille-titre: 28px;
    --align-texte: center;
    --espace-titre-texte: 1px;  /* Nouvelle variable pour l'espacement entre les titres et le texte */
}

/* Styles pour la page de service */
.service-page {
    font-family: Arial, sans-serif;
    padding: 0px;
    background-color: white;
    box-sizing: border-box;
}

/* Espace entre les sections */
.section-service {
    margin-bottom: 50px;
}

/* Image de fond pour certaines sections */
.background-section-service {
    height: 70vh;
    background: url('image2.webp') no-repeat center center;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    padding: 1px;
}

/* Texte au-dessus de l'image de fond */
.overlay-text h1, .overlay-text p {
    text-align: center;
    margin-bottom: var(--espace-titre-texte); 
}

/* Sections avec image et texte côte à côte */
.rectangle02{
    display: flex;
    gap: 20px;
    background-color: #000040;
    color: white;
    padding: 10px;
    border-radius: 70px;
    width: 1000px;
    margin-left: 50px;
}

/* Inversement du texte et de l'image */
.rectangle02.reversed {
    flex-direction: row-reverse;
}

/* Styles pour les conteneurs de texte et d'image */
.text-container, .image-container {
    flex-basis: 50%;
}

/* Styles pour les images dans les sections rectangle */
.image-container img {
    width: 100%;
    height: auto;
    margin-left: 50px;
}

/* Section avec trois boîtes côte à côte */
.white-section-service {
    display: flex;
    gap: 20px;
    justify-content: space-between;
    overflow-y: auto;
}

/* Styles pour chaque boîte dans la section blanche */
.box {
    flex-basis: calc(33.333% - 20px);
    background-color: #000040;
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 75px;
    margin: 40px;
}

/* Styles généraux pour les textes et titres */
.texte {
    color: var(--couleur-texte);
    font-size: var(--taille-texte);
    text-align: var(--align-texte);
    margin-bottom: 2px; 
    
}

.titre {
    margin-left: 50px;
    color: var(--couleur-titre);
    font-size: var(--taille-titre);
    text-align: var(--align-texte);
    margin-bottom: var(--espace-titre-texte); 
}

/* Variations de styles pour les textes et titres */
.texte.centre {
    text-align: center;
}

.texte.grand {
    font-size: 20px;
}

.titre.rouge {
    color: red;
}

/* Styles responsive pour les écrans plus petits */
@media (max-width: 768px) {
    .background-section-service {
        height: 40vh; /* Ajuster la hauteur pour les petits écrans */
    }

    .rectangle02 {
        width: auto; /* Largeur automatique */
        margin: 0 15px; /* Marge pour la visibilité */
        flex-direction: column;
        gap: 10px;
    }

    .overlay-text h1 {
        font-size: 1.5rem;
    }
    
    .white-section-service {
        flex-direction: column;
    }
    
    .box {
        flex-basis: 100%;
        margin: 20px 0;
    }
    
    .rectangle02.reversed {
        flex-direction: column;
    }
    
    .texte, .titre {
        text-align: left; /* Alignement du texte pour les petits écrans */
    }
}

/* Responsive pour très petits écrans */
@media (max-width: 480px) {
    .rectangle02 {
        margin: 0 10px; /* Marge réduite */
    }

    .box {
        margin: 10px 0;
    }

    .titre, .texte {
        font-size: smaller; /* Taille de police réduite pour les très petits écrans */
    }
}
